<template>
    <div id="Title2">
        <newHeader></newHeader>
        <div>
            {{content}}
        </div>
        <foot></foot>
    </div>
</template>

<script>

    import foot from '@/components/Foot'
    import newHeader from '@/components/news/NewsHeader'

    export default {
        name: "Title2",
        components: {
            foot,
            newHeader
        },
        data (){
            return {
                content:"以看到第一个报了错，实际是因为找不到id，getElementById(ID) 并没有找到元素" +
                    "，原因如下在created的时候，视图中的html并没有渲由于此时html已经渲染出来了，所以可以直接操作dom节点，故输" +
                    "出了结果。上就是我自己总结的mounted和mounted的区别，写的比较简陋，记录下来，加深印象。",
            }
        }
    }
</script>

<style scoped>

</style>


