<template>
    <div id="app">
        <!--<router-link :to="{name:'Home'}">-->
        <!--<div style="float: left;margin-right: 80%">-->
        <!--< 返回-->
        <!--</div>-->
        <!--</router-link>-->
        <div>
            <img src="../../assets/logo.jpeg">
        </div>
        <div class="login-field-icon" style="margin: auto">{{title}}</div>
        <div>
            <input class="form-control input-sm" style="margin-top: 3%" v-model="account" placeholder="请输入账号">
        </div>
        <div>
            <input type="password" class="form-control input-sm" style="margin-top: 3%" v-model="password"
                   placeholder="请输入密码">
        </div>

        <div>
            <button class="form-control btn-hg" style="margin-top: 3%" v-on:click="login()">登录</button>
        </div>

        <transition name="fade">
            <p v-if="loginshow" style="margin-top: 3%;color: rgba(240,2,38,0.74);">{{loginstatus}}</p>
        </transition>
        <foot></foot>
    </div>
</template>

<script>
    import foot from '@/components/Foot'

    export default {

        inject:['refresh'],
        components: {
            foot
        },
        data() {
            return {
                title: "Login",
                account: "",
                password: "",
                loginpath: false,
                loginshow: false,
                loginstatus: "",
            }
        },
        beforeMount() {
            //设置定时器，每3秒刷新一次
            var self = this;
            setInterval(checkloginshow, 3000);

            function checkloginshow() {
                if (self.loginshow === true) {
                    self.loginshow = false;
                    self.loginstatus = "";
                }
            }

            checkloginshow();
        },

        methods: {
            // checkloginshow:function(){
            //   if(this.loginshow === true){
            //     this.loginshow = false;
            //     this.loginstatus = "";
            //   }
            // },
            login: function () {
                if (this.account === "") {
                    this.loginshow = true;
                    this.loginstatus = "请输入账号";
                    // alert("请输入账号");
                } else if (this.password === "") {
                    this.loginshow = true;
                    this.loginstatus = "请输入密码";
                    // alert("请输入密码");
                } else {
                    if (this.account !== "admin" || this.password !== "111111") {
                        this.loginshow = true;
                        this.loginstatus = "账号密码错误";
                    } else {
                        this.loginpath = true;
                        if (this.loginpath === true) {
                            this.loginshow = false;
                            this.loginstatus = "登陆成功";
                            this.$emit('userSignIn', this.account, 0, 1);
                            this.$router.push({name: 'User'}),
                            this.refresh();
                        } else {
                            this.loginshow = true;
                            this.loginstatus = "登录过期";
                            // alert("登录过期");
                        }
                    }
                }
            },

            // refresh:function(){
            //     this.$router.go(0);
            // }
        }
    }

</script>

<style scoped>
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
    {
        opacity: 0;
    }
</style>



