<template>
    <div id="user">

        <!--<img src="../assets/logout.jpeg">-->
        <div style="padding: 5% 5% 5% 5%;background-color: #0b2e13"></div>
        <div v-if="islogin">
            <div style="padding-left: 10%;padding-top: 10%;">
                <div style="float: left;width: 20%;">
                    <img src="../../assets/logout.jpeg">
                </div>
                <div style="float: left;padding-left: 7%;padding-top: 5%">
                    <router-link :to="{name:'Login'}">
                        Login
                    </router-link>
                </div>
            </div>
        </div>
        <div v-else>
            <div style="padding-left: 10%;padding-top: 10%;">
                <div style="float: left;width: 20%;">
                    <img src="../../assets/logo.jpeg">
                </div>
                <div style="float: left;padding-left: 7%;padding-top: 5%">
                    <router-link :to="{name:'UserDetail'}">
                        Welcome,{{username}}
                    </router-link>
                </div>
                <div>
                    <button class="form-control btn-hg" style="margin-top: 3%" v-on:click="logout()" v-if="!islogin">登出</button>
                </div>
            </div>
        </div>

        <div>
            <foot></foot>
        </div>
    </div>

</template>

<script>
    import foot from '@/components/Foot'

    export default {
        name: "user",
        inject:['refresh'],
        data() {
            return {
                username: sessionStorage.userName,
                islogin: Number(sessionStorage.loginseen),
            }
        },
        components: {
            foot
        },
        beforeMount() {
            //设置定时器，每10毫秒刷新一次
            var self = this;
            setInterval(updateLogin, 10);

            function updateLogin() {
                this.username = Number(sessionStorage.userName);
                this.islogin = Number(sessionStorage.loginseen);
            }

            // checkloginshow();
        },
        methods: {

            logout: function () {
                this.$emit('userSignIn', this.account, 1, 0);
                this.$router.push({name: 'Login'}),
                this.refresh();
            },
            //刷新页面
            // refresh:function(){
            //     this.$router.go(0);
            // }

        }
    }
</script>

<style scoped>

</style>



