<template>
    <div id="TabBar" class="tab-bar row">
        <div class="col-xs-4 tab-bar-item" @click="didClickedItem(0)" v-bind:class="{active: actives[0]}">

            <div class="row" style="margin-top: 3%">
                <router-link :to="{name:'Home'}">
                    <img src="../assets/home.jpeg">
                </router-link>
                <router-link :to="{name:'OrderList'}" style="margin-left: 18%;margin-right: 18%">
                    <img src="../assets/order.jpeg">
                </router-link>
                <router-link :to="{name:'User'}">
                    <img src="../assets/user.jpeg">
                    <i class="tip" v-show="msgnum > 0">{{msgnum}}</i>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "TabBar",
        props: {},
        data: function () {
            return {
                actives: [true, false, false],
                loginseen: Number(sessionStorage.loginseen),
                userseen: Number(sessionStorage.userseen),
                msgnum:10,
            }
        },
        beforeMount() {
            //设置定时器，每10毫秒刷新一次
            var self = this;
            setInterval(updateLogin, 10);

            function updateLogin() {
                this.loginseen = Number(sessionStorage.loginseen);
                this.userseen = Number(sessionStorage.userseen);
            }

            // checkloginshow();
        },
        methods: {
            didClickedItem: function (tag) {
                this.loginseen = Number(sessionStorage.loginseen);
                this.userseen = Number(sessionStorage.userseen);

                if (tag === 0) {
                    this.actives = this.actives.map(function () {
                        return false;
                    });
                    this.actives[0] = true;
                } else if (tag === 1) {
                    this.actives = this.actives.map(function () {
                        return false;
                    });
                    this.actives[1] = true;
                } else if (tag === 2) {
                    this.actives = this.actives.map(function () {
                        return false;
                    });
                    this.actives[2] = true;
                }
                this.$emit('select-item', tag);
            }
        }
    }
</script>

<style scoped>
    .tab-bar {
        background-color: white;
        height: 54px;
        border: 0 solid rgb(210, 210, 210);
        border-top-width: 1px;
        position: fixed;
        margin: auto;
        bottom: 0;
        width: 100%;
    }

    .tab-bar-item {
        height: 54px;
    }

    .active {
        color: #1E90FF;
    }

    .tip {
        min-width: 20px;
        height: 20px;
        background: red;
        box-sizing: border-box;
        color: white;
        font-size: 10px;
        text-align: center;
        line-height: 20px;
        padding: 0 5px;
        border-radius: 10px;
        display: inline-block;
        position: absolute;
    }
</style>


