<template>
  <div id="app" >

    <keep-alive>
      <!--用户登录-->
      <router-view @userSignIn="userSignIn"></router-view>
    </keep-alive>
    <!--用于处理底部导航栏遮挡页面底部内容-->
    <!--<router-view style="padding-bottom: 54px"/>-->
  </div>
</template>

<script>

export default {
  name: 'App',
  provide(){
    return{
      refresh:this.refresh
    }
  },

  data(){
    return{
      account: sessionStorage.account,
    }
  },

  methods:{
    //子组件(register)将用户名传过来
    //loginseen =1 为需要登录 userseen = 1 为已登录
    userSignIn(userName,loginseen,userseen){
      sessionStorage.setItem("userName",userName);
      sessionStorage.setItem("loginseen",loginseen);
      sessionStorage.setItem("userseen",userseen);
      // sessionStorage.userName = userName;
      // sessionStorage.loginseen = loginseen;
      // sessionStorage.userseen = userseen;
    },
    //刷新页面
    refresh:function(){
      this.$router.go(0);
    }
  }
}
</script>

<style>

  @import "../Flat-UI-master/dist/css/flat-ui.min.css";
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-bottom: 54px;
  min-height: 100vh;/*高度铺满屏幕*/
  background: ghostwhite;
  /*margin-top: 60px;*/
}
</style>



