<template>
    <div id="banner">
        <!--<router-link :to="{name:'Home'}">-->
        <!--<div style="float: left;margin-right: 80%">-->
        <!--< 返回-->
        <!--</div>-->
        <!--</router-link>-->
        <div>
            <img src="../assets/banner.jpeg" style="position:absolute;left: 0%;height: 100vh;width: 100%">
            <img src="../assets/buy.png" style="position:absolute; left: 40%;top:85%;height: 8%" v-on:click="start">
            <!--<btn class="btn" style="position:absolute; left: 40%;top:85%">开始使用</btn>-->
        </div>
    </div>
</template>

<script>
    export default {
        name: "banner",
        data() {
            return {
                title: "Login",
                account: "",
                password: "",
                loginpath: false,
                loginshow: false,
                loginstatus: "",
            }
        },
        methods: {
            start: function () {
                this.$emit('userSignIn', undefined, 1, 0);

                this.$router.push({name: 'Home'})
            },
        }
    }

</script>

<style scoped>
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
    {
        opacity: 0;
    }
</style>



