import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/user/Login'
import User from '@/components/user/User'
import Home from '@/components/Home'
import OrderList from '@/components/order/OrderList'
import App1 from '@/components/app/App1'
import App2 from '@/components/app/App2'
import App3 from '@/components/app/App3'
import App4 from '@/components/app/App4'
import App5 from '@/components/app/App5'
import App6 from '@/components/app/App6'
import App7 from '@/components/app/App7'
import App8 from '@/components/app/App8'
import Banner from '@/components/Banner'
import Title from '@/components/news/Title'
import Title2 from '@/components/news/Title2'
import Title3 from '@/components/news/Title3'
import UserDetail from "@/components/user/UserDetail";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Banner',
      component: Banner
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/user',
      name: 'User',
      component: User
    },
    {
      path: '/user/detail',
      name: 'UserDetail',
      component: UserDetail
    },
    {
      path: '/orderlist',
      name: 'OrderList',
      component: OrderList
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/home/title',
      name: 'Title',
      component: Title
    },
    {
      path: '/home/title2',
      name: 'Title2',
      component: Title2
    },
    {
      path: '/home/title3',
      name: 'Title3',
      component: Title3
    },
    {
      path: '/home/app1',
      name: 'App1',
      component: App1
    },
    {
      path: '/home/app2',
      name: 'App2',
      component: App2
    },
    {
      path: '/home/app3',
      name: 'App3',
      component: App3
    },
    {
      path: '/home/app4',
      name: 'App4',
      component: App4
    },
    {
      path: '/home/app5',
      name: 'App5',
      component: App5
    },
    {
      path: '/home/app6',
      name: 'App6',
      component: App6
    },
    {
      path: '/home/app7',
      name: 'App7',
      component: App7
    },
    {
      path: '/home/app8',
      name: 'App8',
      component: App8
    }
  ]
})



