<template>
    <div id="Title">
        <newHeader></newHeader>
        <div>
            {{content}}
        </div>
        <foot></foot>
    </div>
</template>

<script>

    import foot from '@/components/Foot'
    import newHeader from '@/components/news/NewsHeader'

    export default {
        name: "Title",
        components: {
            foot,
            newHeader
