        data (){
            return {
                content:"大洋网讯 乘坐国际长途航班，怎样既能坐得舒服又能省钱？广报全媒体记者4月11日了解到，近期南航在广州始发的国际航班上推出了“一人多座”新服务，需要的乘客可在航班起飞前于机场柜台额外购买一到三个座位，以获得额外的乘机空间。该服务未来还将在北京、上海等地始发的南航国际航班上推出。\n" +
                    "\n" +
                    "外籍旅客尝鲜新服务：“一人多座”\n" +
                    "\n" +
                    "什么叫“一人多座“？我们先来看看一个案例。\n" +
                    "\n" +
                    "广州日报全媒体记者了解到，南航推出“一人多座”新服务后，一位叫汤米的外国人就“尝鲜”选择多买一个座位。",
            }
        }
    }
</script>

